html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: 'Circe Reg';
  src: url("../../fonts/Circe-Regular.ttf") format("truetype");
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Circe Bold';
  src: url("../../fonts/Circe-Bold.ttf") format("truetype");
  font-weight: bold;
  font-display: swap;
}

body {
  font-family: 'Circe Reg', sans-serif;
  font-size: 18px;
  color: #292B33;
}

img {
  display: block;
  max-width: 100%;
}

.main-header {
  height: 100vh;
  width: 100%;
  position: relative;
  color: white;
}

.header-bg {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  transition: opacity .5s ease-in-out;
}

.main-descriptor {
  font-size: 20px;
  color: #37D078;
  margin-bottom: 10px;
}

.header-social {
  position: absolute;
  bottom: 3%;
  left: 95px;
}

.social {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  margin-left: -10px;
}

.social a {
  margin-right: 20px;
  padding: 10px;
}

.social a:hover svg {
  fill: #37D078;
}

.social svg {
  transition: fill .4s ease;
}

.main-h1 {
  width: 50vw;
  transition: color .4s ease-in-out;
}

.main-h1 .h1-links {
  width: 100%;
  position: relative;
}

.main-h1 .h1-links .line {
  content: '';
  width: 100%;
  height: 3px;
  display: block;
  background: white;
  position: absolute;
  bottom: 15px;
  transition: background .4s ease-in-out, opacity .4s ease-in-out;
}

.main-h1 .h1-links:hover {
  transition: color .4s ease-in-out;
}

.main-h1 .h1-links:hover .line {
  background: #37D078;
}

.icons-items {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 60px;
}

.icons-items .icon-item {
  width: 41%;
  display: flex;
}

.icons-items .icon-item svg {
  width: 6%;
  height: 40px;
  display: block;
  margin-right: 30px;
}

.icons-items .icon-item p {
  margin-top: 25px;
}

.icons-items .icon-item .icon-item-text {
  width: 94%;
}

.navbar {
  width: 100%;
  position: absolute;
  top: 3.7%;
  z-index: 10;
  color: white;
}

.navbar .nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar nav {
  width: 50%;
}

.navbar nav ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.navbar nav ul li {
  cursor: pointer;
  transition: color .4s ease;
}

.navbar nav ul li:hover {
  color: #37D078;
}

button {
  outline: none;
  background: none;
  color: inherit;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.border-button {
  border: 1px solid white;
  border-radius: 5px;
  padding: 15px 54px;
  transition: color .5s ease-in-out;
  background: none !important;
}

.border-button svg {
  width: 100%;
  height: 100%;
  fill: white !important;
}

.border-button:hover {
  color: #292B33;
}

.button {
  border-radius: 5px;
  padding: 12px 35px;
  background: #292B33;
  color: white;
  display: block;
  width: fit-content;
  font-size: 16px;
  position: relative;
  overflow: hidden;
}

.button .bg {
  position: absolute;
  display: block;
  width: 0px;
  height: 0px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: width .5s ease-in-out, height .5s ease-in-out;
  z-index: 1;
}

.button .bg svg {
  fill: #37D078;
  width: 100%;
  height: 100%;
}

.button:hover .bg {
  width: 300%;
  height: 700px;
}

.button .text {
  position: relative;
  z-index: 2;
}

.underline-button {
  transition: color .4s ease-in-out;
  overflow: hidden;
}

.underline-button::after {
  content: '';
  display: flex;
  width: 100%;
  height: 1px;
  background: white;
  margin-top: 3px;
  font-size: 18px;
  transition: background .5s ease-in-out;
}

.underline-button::after.opacity {
  opacity: 0.3;
}

.underline-button:hover {
  color: #37D078;
}

.underline-button:hover::after {
  animation: button-line 1s ease-in-out;
  background: #37D078;
}

@keyframes button-line {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(100%);
  }
  51% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}

.scroll-down {
  position: absolute;
  bottom: 3%;
  left: 50%;
  transform: translateX(-50%);
  width: 1px;
  height: 90px;
  background: rgba(255, 255, 255, 0.4);
  opacity: 1;
  overflow: hidden;
}

.scroll-down:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  height: 100%;
  width: 100%;
  animation: scrolling 2.5s infinite cubic-bezier(0.645, 0.045, 0.355, 1);
}

@keyframes scrolling {
  0% {
    transform: translateY(-100%);
  }
  70% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(100%);
  }
}

@media (max-width: 1375px) {
  .scroll-down {
    height: 70px;
  }
}

.e-label {
  position: absolute;
  right: 200px;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.7);
  bottom: 3vh;
  line-height: 1em;
}

.e-label svg {
  width: 18px;
  fill: rgba(255, 255, 255, 0.7);
  transition: fill .3s ease;
}

.e-label span {
  display: inline-block;
  margin-left: 12px;
  margin-top: 2px;
}

.e-label:hover svg {
  fill: #A03ADB;
}

.container {
  position: relative;
  padding-left: 200px;
  padding-right: 200px;
  width: 100%;
  height: 100%;
}

.container.nav-container {
  padding-left: 95px;
  padding-right: 95px;
}

.container.header-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container.section-container {
  padding-top: 140px;
}

.container.section-container.last {
  padding-bottom: 140px;
}

.default-content-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.default-content-wrapper.reverse {
  flex-direction: row-reverse;
}

.default-content-wrapper p {
  margin-top: 35px;
}

.default-content-wrapper .default-content-left {
  width: 38%;
}

.default-content-wrapper .default-content-right {
  width: 50%;
}

.default-content-wrapper .default-content-right img {
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

.default-content-wrapper .button {
  margin-top: 50px;
}

.section {
  position: relative;
}

.section.with-bg {
  background-repeat: no-repeat;
  background-size: cover;
  padding: 180px 0px;
}

.section.with-bg .container {
  display: flex;
  justify-content: flex-end;
}

.section.with-bg .with-bg-block {
  color: white;
  background: #292B33;
  padding: 100px 100px;
  padding-right: 170px;
  width: 51vw;
}

.section.with-bg .with-bg-block p {
  margin-top: 35px;
}

.main-footer {
  background: #292B33;
  padding: 100px 0;
  padding-bottom: 140px;
  position: relative;
  color: white;
}

.main-footer hr {
  margin-top: 100px;
  opacity: .1;
}

.footer-top {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.footer-top-left {
  width: 40%;
}

.footer-top-left p {
  margin-top: 25px;
}

.footer-top-left button {
  margin-top: 50px;
}

.footer-top-right {
  width: 32%;
}

.footer-top-right span {
  margin-top: 14px;
  display: inline-block;
  opacity: .8;
}

.footer-contant-wrapper {
  margin-bottom: 50px;
}

.footer-bottom {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}

.footer-bottom button {
  margin-top: 30px;
}

.footer-bottom-left {
  width: 8%;
}

.footer-bottom-right {
  width: 80%;
}

.representations {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.representations p {
  margin-left: 90px;
  width: 30%;
  line-height: 1.7em;
  opacity: .8;
}

a {
  text-decoration: none;
  color: inherit;
}

h1, h2, h3 {
  font-weight: normal;
}

h1 {
  font-size: 62px;
  line-height: 130%;
}

h2 {
  font-size: 42px;
  line-height: 120%;
}

.default-text {
  font-size: 18px;
  line-height: 170%;
  color: #454547;
}

.default-text.white {
  color: white;
}
